      h1 Generalidades del turismo y los viajes

    .row.mb-5
      .col-lg-7
        p.mb-3  Aunque existen conceptos tan sencillos como el que presenta el diccionario de la Real Academia de la Lengua Espaola (RAE) o la universidad de Oxford refirindose al turismo como la actividad de viajar; tambin existen otros autores que se han tomado el trabajo de profundizar un poco en las acciones que se ejecutan cuando hacemos turismo. Es as como la Organizacin Mundial de Turismo (s.f.) propone: 
        p.text-bold  El turismo es un fenmeno social, cultural y econmico relacionado con el movimiento de las personas a lugares que se encuentran fuera de su lugar de residencia habitual por motivos personales o de negocios/profesionales. Estas personas se denominan visitantes y el turismo tiene que ver con sus actividades, de las cuales algunas implican un gasto turstico.
      .col-lg-5
        figure(data-aos="fade-right")
          img(src="@/assets/curso/t1-im1.jpg", alt="Mujer mirando panoramica de una ciudad")

    .row.mb-5.justify-content-center
      .col-lg-10
        .cajon.color-secundario.p-4
          p No obstante, todos los autores concluyen en reconocer <b>el turismo como una actividad econmica de gran dinamismo</b>, ya que incide directamente en otros sectores como el de <b>la construccin, transporte, telecomunicaciones, salud, educacin, entre otros. </b>

    p.mb-5  Se presentan algunas de las principales caractersticas del turista actual:

    figure.mb-5(data-aos="fade-right")
      img(src="@/assets/curso/t1-info1.svg", alt="Cuadro informativo con los tipos de cliente")

    .row.mb-5.justify-content-center
      .col-lg-10
        .cajon.color-secundario.p-4
          p En las dos ltimas dcadas, el turismo ha recobrado una mayor importancia, probando ser el sector de expansin que contribuye al desarrollo de <b>un pas y cuyas acciones articulan al Gobierno, empresa privada y comunidades.</b>

    #t_1_1.titulo-segundo
      h2 1.1  Sistema turstico
    
    p.mb-5  Muchos autores han querido expresar un concepto que refiere todos los componentes del sistema turstico, pero an no se tiene un consenso claro al respecto.
    .row.mb-5
      .col-lg-7
        p.mb-3  El sistema turstico es segn la OMT (2019):
        p.mb-3.text-bold  Es un conjunto de elementos interrelacionados entre s que evolucionan dinmicamente. Las caractersticas de una comunidad local, los recursos humanos, naturales y culturales de un territorio se combinan formando la materia prima de la actividad turstica. Todos los elementos se conectan y se entrelazan en el mercado turstico, y resulta en un desplazamiento del turista desde el lugar de residencia habitual hasta el destino elegido en un circuito que se retroalimenta.
        p Todo esto es vlido, teniendo en cuenta que el turismo como elemento dinamizador de la economa moviliza recursos econmicos, tcnicos y humanos, entrelazando actividades y generando una gran logstica que permite que alrededor de la actividad se beneficien muchos sectores y subsectores.
      .col-lg-5
        figure.mb-5(data-aos="fade-right")
          img(src="@/assets/curso/t1-i2.jpg", alt="Cuadro informativo con los tipos de cliente")
    p.mb-5  Se propone a continuacin un esquema que identifica algunos de los actores que intervienen en el desarrollo del turismo, los mismos sern descritos posteriormente de manera independiente.

