        p.mb-3  Est conformada por Bahas, Puntas, Cayos, Cayos del Norte, Cayos del Sur, con un clima clido que va desde los 26 a los 34 grados, tiene una temporada seca entre los meses de diciembre y mayo y la temporada de lluvia que se da en los meses de junio a noviembre, principalmente en el mes de junio.
