    p.mb-5  <b>El turismo de salud es su principal eje</b>, esto segn cifras del clster de servicios de medicina y odontologa de Medelln, que informa que esta modalidad de turismo viene en crecimiento en los ltimos aos, de pacientes extranjeros que optan por realizarse algn tratamiento mdico u odontolgico. Pero no solo el turismo de salud es atractivo en esta ciudad, hay variedad de eventos, festivales, y lugares para visitar, entre ellos:
