    h4.titulo-cuarto  Cali
    p.mb-4  Es la tercera ciudad ms poblada de Colombia al suroeste de la capital, posee una gran variedad de clubes nocturnos, centros comerciales, culturales, restaurantes que desde hace unos aos ha crecido en su variedad para todos los gustos, conocida tambin como la capital de la salsa del que ofrece una gran cantidad de lugares como en Juanchito zona con ms clubes de salsa.
    p.mb-5  En el ao 2019 fue recibi el reconocimiento como la mejor ciudad cultural de Suramrica, adems de ser el principal centro urbano del pas, tiene actividades para todos los gustos, si de naturaleza se trata, en Cali est el Parque Nacional Natural Farallones de Cali, compuesto de una majestuosa cadena de montaas que hace parte de la Cordillera Occidental, separadas por los ros Cauca y la cuenca del Pacfico, de aqu nacen aproximadamente 30 ros que abastece el suroccidente colombiano, cabe destacar que posee una gran variedad de especies endmicas de aves.
