              p nico en albergar gran variedad de ecosistemas entre el marino hasta el selvtico, considerado el parque ms diverso del mundo.

          .row(titulo="Parque Nacional Natural Gorgona")
            .col-md-12
              p Encontramos arrecifes coralinos y bosque hmedo, en los aos 60's exista la penitenciara Gorgona que estuvo activa hasta los aos 80' s, ahora es un lugar turstico para los visitantes.     
          .row(titulo="Parque Nacional Natural Tatam")
            .col-md-12
              p Ubicado en la cordillera Occidental con gran variedad de especies tanto en flora como en fauna, all se encuentra el Pramo Tatam, ubicado en los Departamentos del Valle del Cauca, Choc y Risaralda.
          .row(titulo="Parque Nacional Natural Sanquianga")
            .col-md-12
              p En su mayora ecosistema Manglar donde es el 20 % del total pacfico en el pas, es la base de alimentacin y reproduccin de especies marinas y de estuarios, tambin de anidacin de aves.     
          .row(titulo="Parque Nacional Natural de los Katos")
            .col-md-12
              p Ubicado en la regin del Darin y Urab, tiene el ttulo de Patrimonio Mundial Natural y Reserva de la Biosfera, actualmente se ubica all la comunidad indgena Wounaan de Juin Phubuur y la comunidad Peranchito de la etnia Embera-Katio.
          .row(titulo="Parque Nacional Natural Uramba Baha Mlaga")
