          .row(titulo="Parque nacional natural Chingaza")
            .col-md-12
              p En su dimensin comprende los Departamentos de Cundinamarca y Meta, se cree que las especies de flora presentes en el parque sobrepasan a las 2000, segn estudios realizados por expertos en este tipo de vegetacin.
    
